{
padding :0;
margin :0;
color:white;
}

.HeaderBody{
    background-color: #15384C;
    height: 80px;
    width: 100%;
    color: white;
    top: 0;

    z-index: 999;
    position: fixed;
}
a{
    text-decoration: none;
    color: white;
}
.navBar{
display: flex;
    text-align: center;
    justify-content: space-around;
    margin-top: 25px;
    transition: 0.3s;
}
.navBarMain {
    transition: 0.1s;
    display: flex;
    text-align: center;
    justify-content: space-around;
    width: 600px;

}
.navBarMain a{
    padding: 0 10px;

}


.navBarOther{
    display: flex;
    text-align: center;
    justify-content: space-around;
    width: 200px;


}

.logo{
    flex-wrap: wrap;
    z-index: 999;
    top: 0;
    transition: 0.3s;
    transform: translateY(-40px);
    min-width: 150px;
    height: 80px;
 margin-top: 10px;


}
@media screen and (max-width: 880px) {
    .navBarMain{
        width: 10px;
        visibility: hidden;


    }

}
@media screen and (max-width: 1080px) {
    .navBarMain a:nth-child(5){
       display: none;

    }

}



















