.movie img{
    max-width: 200px;
    max-height: 280px;
    height: 280px;
    width: 200px;


}
.title{
    color: white;

}
.movie {
    max-height: 400px;
    padding: 5px;
    cursor: pointer;
}


.movie div:hover h4{
    color: #20BEC6 ;

    overflow: hidden;

}

.divTR{

    max-width: 200px;
    max-height: 280px;
    height: 280px;
    width: 200px;
    overflow: hidden;
}

.movie:hover .transf{
    transform: scale(1.2);

}
.movie:hover .divTR{
    box-shadow: 0 0  10px 5px #20BEC6;
}




.description {
    max-width: 200px;
    width:100%;
    height:100%;

    }

.genre{
    color: grey;
}
img{
    border-radius: 11px;
transition: 0.5s;
}